.singupscreen {
  max-width: 300px;
  padding: 50px;
  margin: -60px auto;
  background: rgba(0, 0, 0, 0.25);
}
.singupscreen form {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: baseline;
}
.singupscreen form h2 {
  font-weight: 900;
  padding: 20px;
  margin: 0px auto 20px auto;
}
.singupscreen form input {
  padding: 5px 20px;
  margin: 10px auto 20px auto;
  border-radius: 22px;
  border: none;
  outline: none;
  background: rgba(0, 0, 0, 0.384);
  color: #fff;
  transition: all 400ms ease-in;
}
.singupscreen form input:hover {
  background: rgba(0, 0, 0, 0.4);
}
.singupscreen form button {
  padding: 8px 20px;
  margin: 0 auto;
  background: none;
  outline: none;
  color: blanchedalmond;
  border-radius: 36px 16px;
  transition: all 400ms ease-in;
}
.singupscreen form h4 {
    font-weight: 700;
}
.singupscreen form button:hover {
  background: rgba(84, 209, 151, 0.589);
}
.createnewaccount {
    color: rgba(255, 255, 255, 0.274);
    transition: all 400ms ease-in;
}
.createnewaccount:hover {
    color: #fff;
    text-decoration: underline darkgreen;
    text-shadow: 1px 2px 4px 4px black;
}
