.profilescreen {
  background-color: rgba(0, 0, 0, 0.411) !important;
  color: rgba(240, 247, 240, 0.479);
  width: 100%;
  height: 100vh;
}

.profilescreen-body h1 {
  margin: 10px auto;
  padding-top: 60px;
  border-bottom: 0.4px solid #999;
  width: 60vw;
}
.profilescreen-info {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.profilescreen-info-info {
  flex: 2;
  margin: 0px 30px 10px -20px;
  padding: 10px;
}
.profilescreen-info-info h2 {
  width: 70%;
  padding: 4px 20px;
  background: #333;
  color: #fff;
  border-radius: 24px;
}
.profilescreen-info-info h5 {
  text-align: center;
  width: 65%;
  margin: 5px 20px;
  padding: 10px;
  color: #fff;
  border-bottom: 0.5px solid #999;
}
.profilescreen-info-info p {
  width: 65%;
  margin: 0px 20px 20px 15px;
  padding: 5px;
  color: #fff;
}
.profilescreen-info-info h3 {
  width: 90%;
  margin: 0px 20px 0px 15px;
  padding: 5px;
  color: #fff;
}
.profilescreen-info-info h6 {
  width: 65%;
  margin-left: 15px;
  margin-bottom: 20px;
  padding: 5px;
  color: #fff;
}
.profilescreen-info-info .userplan {
  display: flex;
  justify-content: space-between;
}
.profilescreen-info-info .userplan button {
  padding: 8px 25px;
  display: block;
  margin-bottom: 50px;
  margin-top: 2px;
  background: #999;
  border-radius: 20px 40px;
  outline: none;
  border: none;
}
.profilescreen-info-info .userplan button:hover,
.profilescreen-info-info button:hover {
  background: rgba(84, 209, 151, 0.589);
  color: black;
}
.profilescreen-info-info > button {
    width: 300px;
  background: #999;
  padding: 8px 25px;
  border-radius: 20px 40px;
  outline: none;
  border: none;
}

.profilescreen-info img {
  border-radius: 2px 10px 10px 100px;
  padding: 4px;
  margin: 10px;
  width: 30%;
  height: auto;
  object-fit: contain;
}

.profilescreen-pack-info {
    margin: 10px auto;
    text-align: center;
    font-size: 12px;
    font-weight: 400;
}