.loginscreen {
  position: relative;
  color: #fff;
  height: 100vh;
  background: url("https://theplanetd.com/images/Cappadoccia-Turkey-Bucket-List.jpg")
    center no-repeat;
  background-size: cover;
}
.loginscreen-body {
     display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 35%;
    z-index: 1;
    color: #fff;
    padding: 18px;
    width: 100%;

}

.loginscreen-body > h1 {
    font-size: 2.4rem;
    margin-bottom: 20px;
} 
.loginscreen-body > p {
    margin-bottom: 20px;
    font-size: 1.15rem;
}

.logingscreen-logo {
  position: fixed;
  left: 0;
  width: 150px;
  object-fit: contain;
  padding-left: 6px 8px;
}
.loginscreen-singin-btn {
  position: fixed;
  right: 26px;
  top: 3px;
  padding: 6px 8px;
  font-size: 1rem;
  background-color: #e50914;
  font-weight: 700;
  color: #fff;
  margin: 4px;
  border: none;
  border-radius: 1.1rem;
  cursor: pointer;
  transition: all 250ms ease-in;
}
.loginscreen-singin-btn:hover {
  border-radius: 2.1rem;
  background: transparent;
}
.loginscreen-gradient {
  width: 100vw;
  height: 100vh;
  z-index: 1;
  background: rgba(0, 0, 0, 0.4);
      background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.6) 0%,
    rgba(0, 0, 0, 0) 60%,
    rgba(0, 0, 0, 0.8) 100%
  );
}

.login-input {
    padding: 8px;
    margin-bottom: 30px;
    margin-top: 30px;
    border-radius: 24px;
    border: none;
    background:rgba(0,0,0,0.6);
    color: tomato;
    width: 90%;
    outline: none;
}

.loginscreen-getstarted {
    position: relative;
    text-align: center;
    background: transparent;
    padding: 10px;
    font-size: 1.2rem;
    font-weight: 900;
    color: wheat;
    border: 0.2px double #888;
    border-radius: 35px 16px;
    outline: none;
    margin-left: 120px;
    transition: all 200ms ease-in-out;
}
.loginscreen-getstarted:hover {
    background-color: rgba(100, 148, 237, 0.5);
}

