.paymentsuccessscreen {
  width: 100vw;
  height: 100vh;
  background-color: GRAY;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.paymentsuccessscreen h1 {
  margin-bottom: 40px;
}
.paymentsuccessscreen button {
  padding: 10px 25px;
  background-color: rgb(168, 164, 164);
  border: none;
  outline: none;
  text-align: center;
  border-radius: 35px 16px;
  margin-top: 60px;
  margin-bottom: 20px;
  transition: all 200ms ease-in-out;
  z-index: 1;
}

.paymentsuccessscreen button:hover {
  background-color: rgba(100, 148, 237, 0.5);
  color: white;
}
